@import "../../styles/helpers/_helpers";

.footer {
	background-color: #3c4658;
	background-image: url(../img/footer.jpg);
	background-repeat: no-repeat;
	background-position: center 0;
	padding: rem(40px 0);
	display: grid;
	grid-template-columns: 1fr repeat(var(--grid-column), var(--grid-width)) 1fr;
	gap: 0 var(--grid-gap);
	grid-column: 1/-1;
	color: #fff;
	h4, a {
		color: #fff;
	}
	a {
		border-color: #fff;
		&:hover {
			border-color: transparent;
		}
	}
	h4 {
		position: relative;
		margin: 0;
		font-weight: bold;
		font-size: var(--font-size-lg);
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
		padding-bottom: rem(20px);
		margin-bottom: rem(20px);
		&::after {
			content: "";
			position: absolute;
			bottom: rem(-1px);
			left: rem(1px);
			width: rem(32px);
			height: rem(3px);
			background-color: #00c658;
		}
	}
	.teaser-news {
		border-bottom: 1px solid rgba(255, 255, 255, 0.2);
		padding-bottom: rem(20px);
		margin-bottom: rem(20px);
		@include last(1) {
			border-bottom: none;
			padding-bottom: 0;
			margin-bottom: 0;
		}
	}
}
.footer__col_one {
	grid-column: 2/span 3;
	font-weight: 600;
	font-size: rem(12px);
	display: grid;
	gap: rem(24px);
	align-content: flex-start;
	a {
		border: none;
	}
}
.footer__col_two {
	grid-column: 5/span 3;
}
.footer__col_three {
	grid-column: 8/span 3;
}
.footer__col_four {
	grid-column: 11/span 3;
}
.footer__phone {
	font-weight: bold;
}
.footer__pdf {
	margin-bottom: rem(34px);
	display: block;
	border: none;
	padding-left: rem(34px);
	font-weight: bold;
	font-size: var(--font-size-sm);
	position: relative;
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		@include sprite($pdf);
	}
}
.footer__phone {
	position: relative;
	padding-left: rem(24px);
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		@include sprite($phone);
	}
}
.footer__email {
	position: relative;
	padding-left: rem(26px);
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		@include sprite($email);
	}
}
.footer__location {
	position: relative;
	padding-left: rem(26px);
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		@include sprite($location);
	}
}
.footer__time {
	position: relative;
	padding-left: rem(26px);
	&:before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		@include sprite($time);
	}
}